  svg = d3.select("svg");
  width = +svg.attr("width");
  height = +svg.attr("height");
  var path = d3.geoPath();
  var projection = d3.geoMercator()
  .scale(60)
  .center([0,20])
  .translate([width / 2, height / 2]);

  //remove all children of svg to clear the graph
  svg.selectAll("*").remove();
 
  // Data and color scale
  loadDepth(currentDepth)
  let developer_count_list = [];
  Object.keys(choropleth_data).forEach((key)=> {developer_count_list.push(choropleth_data[key])})
  console.log(developer_count_list)

  //colorScale =  d3.scaleQuantile()
