  //set up color scale
  let scaleFunc = (x)=> {return Math.cbrt(x)}
  colorScale = d3.scaleLinear()
  .domain([0, scaleFunc(d3.max(developer_count_list))])
  .range(["white", "orange"])
 
  svg.append("g")
    .selectAll("path")
    .data(topo.features)
    .enter()
    .append("path")
    // draw each country
    .attr("d", d3.geoPath().projection(projection))
    // set the color of each country
    .attr("fill", function (d) {
      d.total = choropleth_data.get(d.id) || 0;
      return colorScale(scaleFunc(d.total));
    })
    //.on("mouseover", mouseover)
    //.on("mousemove", mousemove)
    //.on("mouseleave",mouseleave)
    ;
