var depth_button = document.getElementById("depth-button")
var depth_label = document.getElementById("depth-button-label")
depth_button.onclick = function increaseDepth() {
  if(currentDepth == total_depth) return;
  if (currentDepth==total_depth-1){
    
    document.getElementById("depth-button").disabled = true;
    document.getElementById("continue-btn").classList.remove('disabled');
  }
  setDepth(currentDepth+1);
  updateChoropleth()
}

